import React, { useState, useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import useSWR from 'swr'
import { verifyEmail } from '../api/User'
import CircleLoader from "react-spinners/CircleLoader";
import Helmet from "react-helmet";

export default function VerifyEmail() {
  let { token } = useParams()

  const { data, error } = useSWR(process.env.REACT_APP_BACKEND + '/user/verify-email/', async url => await verifyEmail(url, { token: token }), { revalidateOnFocus: false, revalidateOnReconnect: true })

  useEffect(() => {
    const darkMode = JSON.parse(localStorage.getItem('md'))
    const root = window.document.documentElement
    if (darkMode) {
      root.classList.add('dark')
    } else {
      root.classList.remove('dark')
    }
  }, [token])

  return (
    <>
      <Helmet>
        <title>Email Verification | Aakhyaan Academy</title>
      </Helmet>
      <div className={`font-sans bg-back dark:bg-background dark:text-mainText w-full h-full flex pt-[6vh] md:pt-[7vh]`}>
        <div className='m-auto flex flex-col items-center gap-5'>
          {!data && !error ?
            < CircleLoader color="blue" size={50} />
            :
            <span className='text-warningText'>
              {data?.message}
            </span>
          }
          <Link to="/" className="text-center font-sans text-xl hover:underline transition-all dark:text-blue-500 text-blue-600">
            Go Home
          </Link>
        </div>
      </div>
    </>
  )
}
